	module.exports = {"pathContext":{"abstract":"Web Audio is by design an object-oriented, imperative API offering low-level control over audio graphs. There have been a number of efforts to provide a more intuitive wrapper API. Designing such wrapper libraries poses challenges in addressing graph configuration, dynamic mutation and data flow. Syntax of creating directed graphs in imperative code is not representative of the complex graph shapes, making the code difficult to understand without external visualisation tools.\n\nIn this paper we describe r-audio, a Web Audio wrapper library which attempts to solve the issues of imperative graph representations by leveraging the component system of React. We compare approaches of existing wrapper libraries and discuss solutions to specific issues of declarative and reactive representations of Web Audio graphs. We evaluate r-audio in terms of the ability to create arbitrary directed graphs and mutate them in real time.","authors":[{"link":"http://fiala.space/","name":"Jakub Fiala"}],"slug":"r-audio-declarative-reactive-and-flexible-web-audio-graphs-in-react","title":"r-audio: Declarative, reactive and flexible Web Audio graphs in React","type":"paper"}}

